/* important section */
.jsmind-inner {
  position: relative;
  overflow: auto;
  width: 100%;
  height: 100%; }

/*box-shadow:0 0 2px #000;*/
.jsmind-inner {
  moz-user-select: -moz-none;
  -moz-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

/* z-index:1 */
canvas {
  position: absolute;
  z-index: 1; }

/* z-index:2 */
jmnodes {
  position: absolute;
  z-index: 2;
  background-color: transparent; }

/*background color is necessary*/
jmnode {
  position: absolute;
  cursor: default;
  max-width: 400px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis; }

jmexpander {
  position: absolute;
  width: 11px;
  height: 11px;
  display: block;
  overflow: hidden;
  line-height: 12px;
  font-size: 12px;
  text-align: center;
  border-radius: 6px;
  border-width: 1px;
  border-style: solid;
  cursor: pointer; }

/* default theme */
jmnode {
  padding: 10px;
  background-color: #fff;
  color: #333;
  border-radius: 5px;
  -webkit-box-shadow: 1px 1px 1px #666;
          box-shadow: 1px 1px 1px #666;
  font: 16px/1.125 Verdana,Arial,Helvetica,sans-serif; }

jmnode:hover {
  -webkit-box-shadow: 2px 2px 8px #000;
          box-shadow: 2px 2px 8px #000;
  background-color: #ebebeb;
  color: #333; }

jmnode.selected {
  background-color: #11f;
  color: #fff;
  -webkit-box-shadow: 2px 2px 8px #000;
          box-shadow: 2px 2px 8px #000; }

jmnode.root {
  font-size: 24px; }

jmexpander {
  border-color: gray; }

jmexpander:hover {
  border-color: #000; }

@media screen and (max-device-width: 1024px) {
  jmnode {
    padding: 5px;
    border-radius: 3px;
    font-size: 14px; }
  jmnode.root {
    font-size: 21px; } }

